            </v-tab>

            <v-tab :disabled="advertisingProgram === ''">
              Шаг 3. Лифты
            </v-tab>

          </v-tabs>

          <v-tabs-items v-model="tab">
            <!--  Шаг 1 список РП -->
            <v-tab-item>
              <v-card
                  color="basil"
