              >
                <!--фильтры-->
                <div class="row">

                  <div class="col-3">
                    <v-text-field label="Поиск по клиенту" @keydown.enter="getAdvertisingProgramList"
                                  @blur="getAdvertisingProgramList"
                                  v-model="filterAndSortRp.clientName"></v-text-field>
                  </div>

                  <div class="col-3">
                    <v-select
                        label="Тип клиента"
